$mobile:640px;
$tablet:1024px;
$desktop:1025px;

@mixin desktop{
    @media screen and (min-width:$desktop){
        @content;
    }
}
@mixin tablet{
    @media screen and (max-width:$tablet){
        @content;
    }
}
@mixin mobile{
    @media screen and (max-width:$mobile){
        @content;
    }
}