                button{ width: 150px;}
            }
        }
        .footer_bottom{
            li{
                &:first-child{
                    margin-top: 10px;
                }
                padding-left: 20px;
                p:first-of-type>span{
                    padding-left: 0px;
                }
                p:nth-of-type(2)>span::before{
                    content: "|";
                    padding-right: 20px;
                    color: #000;
                }
            }
        }
    }
    #sub_page{
        padding-top: 150px;
        .Food{
            width: 550px;
            margin: 0px auto;
            margin-top: 50px;
            ul{
                grid-template-columns: 1fr 1fr;
            }
        }
    }
    #detail{
        width: 550px;
        margin: 0px auto;
        padding-top: 150px;
        .product_box{
            .product_img{
                img{
                    width: 99%;
                    object-fit: contain;
                }
            }
        }
    }
    #Cart{
        width: 550px;
        margin: 0px auto;
        padding-top: 150px;
        table{
            thead{
                th{ width: 10%; }
            }
            tbody{
                button{
                    background: #fff;
                    border: $border;
                    border-radius: 5px;
                }
                td:nth-of-type(4){
                    display: flex;
                    justify-content: space-around;
                    align-items: center;
                    span{
                        display: block;
                        width: 20px;
                        height: 30px;
                        line-height: 30px;
                        margin: 0px
                    }
                    button{
                        width: 30px;
                        height: 30px;
                        margin: 0px 10px;
                    }
                }
            }
        }
        .totalPrice{
            h3{ 
                font-size: 1.875rem;
                text-align: right;
                padding: 50px 0px;
            }
        }
        .jumon_box{
            padding-top: 50px;
            border-top: $border;
            width:100%;
            height: 50px;
            .jumon{
                width: 200px;
                height: 50px;
                position: absolute;
                transform: translateX(-50%);
                left: 50%;
                background: #555;
                color: #fff;
                border-radius: 3px;
            }
        }
    }
}
